  <label style="font-weight:bold;">最近更新时间：{{upDataTime}}</label>
 <div style="display: inline;">
  
</div>
<button v-on:click="showInputNumerOfItems=!showInputNumerOfItems" type="button" class="btn btn-default" aria-label="Left Align" style="position:fixed;right:0px">
  <span class="glyphicon glyphicon-align-left" style="font-weight:bold;" aria-hidden="true"><i class="fa fa-cog" aria-hidden="true"></i></span>
</button>
<div v-show="this.showInputNumerOfItems" style="width:250px;height:30px;z-index:5;position:fixed;right:0px;top:0px" >
