    <input v-on:click="inputItems"  style="widht:20px; display: inline;" type="button" value="确定">
  </div>

 
    <div class="tablediv" v-on:click="clickBody">
    <!-- <label>滚动速度（单位：秒）</label>
    <select v-model="optionValue" style="width:100px"> 
      <option  disabled value="0">请选择</option>
      <option v-for="(item,index) in selectList" :key="index" :value="index+1" >
        {{item}}
      </option> 
    </select> --> 
    <!-- <input v-on:click="test" type="button"  value="确定">    -->
    <table class="table table-bordered" style=" margin-bottom: 0rem;text-align: center;font-size: 15px">   
      <thead >    
              <tr >                
              <th>到达时间</th>
              <th>进港航班号</th>   
              <th>起始地</th>
              <th>起飞时间</th>
              <th>出港航班号</th>  
              <th>目的地</th>
              <th>机号</th>  
              <th>机型</th>
              <th>属性</th>  
              <th>航班状态</th>
              <th>公务机</th>  
              <th>VIP</th>
              <th>已分配停机位</th>       
              <th>结果状态</th>
              <th>新分配停机位</th>         
              </tr>
      </thead> 
       
      <tbody >          
          <!-- class="list-complete-item" -->
          <!-- <transition-group name="list-complete" tag="p"> -->
          <tr v-for="(value,index) in items " :key="index"  style="height:10px"> 
<!-- 
            <td>{{value.atime}}</td>
            <td>{{value.aflightno}}</td>
            <td>{{value.Forg}}</td>
            <td>{{value.dtime}}</td>
            <td>{{value.dflightno}}</td>
            <td>{{value.Fest}}</td>
            <td>{{value.planeno}}</td>
            <td>{{value.mdl}}</td>
            <td>{{value.nation}}</td>       
            <td>{{value.business}}</td>         
            <td>{{value.paras}}</td>    
            <td >{{value.IsVip}}</td>          
            <td>{{value.ParkingGate}}</td>  
            <td v-if="value.resulttype=='成功'" style="color: rgb(0, 255, 13);font-weight: bolder">成功</td>  
            <td v-if="value.resulttype=='失败'" style="color: rgba(250, 10, 42, 0.966);font-weight: bolder">失败</td>       
            <td v-if="value.resulttype=='已分配'" style="color:gainsboro;font-weight: bolder">已分配</td>  
            <td>{{value.result}}</td> -->

            <!-- 对照组 -->
            <!-- <td>{{value.atime}}</td> -->
            <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.atime}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.atime}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.atime}}</td>  


            <!-- <td>{{value.aflightno}}</td> -->
            <td v-if="value.resulttype=='成功'" class="tdstyle1" style="color:blue">{{value.aflightno}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2" style="color:blue">{{value.aflightno}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3" style="color:blue">{{value.aflightno}}</td>  

            <!-- <td>{{value.Forg}}</td> -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.Forg}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.Forg}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.Forg}}</td>

            <!-- <td>{{value.dtime}}</td> -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.dtime}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.dtime}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.dtime}}</td>

            <!-- <td>{{value.dflightno}}</td> -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1" style="color:blue">{{value.dflightno}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2" style="color:blue">{{value.dflightno}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3" style="color:blue">{{value.dflightno}}</td>

            <!-- <td>{{value.Fest}}</td> -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.Fest}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.Fest}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.Fest}}</td>


            <!-- <td>{{value.planeno}}</td> -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.planeno}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.planeno}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.planeno}}</td>

            <!-- <td>{{value.mdl}}</td> -->
            <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.mdl}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.mdl}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.mdl}}</td>

            <!-- <td>{{value.nation}}</td>        -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.nation}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.nation}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.nation}}</td>


            
            <!-- <td>{{value.paras}}</td>     -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.paras}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.paras}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.paras}}</td>

            <!-- <td>{{value.business}}</td>          -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.business}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.business}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.business}}</td>



            <!-- <td >{{value.IsVip}}</td>           -->
             <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.IsVip}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.IsVip}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.IsVip}}</td>
            <!-- <td>{{value.ParkingGate}}</td>   -->
            <td v-if="value.resulttype=='成功'" class="tdstyle1">{{value.ParkingGate}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">{{value.ParkingGate}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">{{value.ParkingGate}}</td>

            <td v-if="value.resulttype=='成功'" class="tdstyle1">成功</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2">失败</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3">已分配</td>  
            
            <!-- <td>{{value.result}}</td> -->
            <td v-if="value.resulttype=='成功'" class="tdstyle1" style="color:red">{{value.result}}</td>  
            <td v-if="value.resulttype=='失败'" class="tdstyle2" style="color:red">{{value.result}}</td>       
            <td v-if="value.resulttype=='已分配'" class="tdstyle3" style="color:red">{{value.result}}</td>  



        </tr>
          <!-- </transition-group> -->
      </tbody>
    </table>
    <div v-show="showLoading" style="height:400;width:600;  font-size: 100px;text-align: center;margin-top:100px">正在加载，请等待......</div>
    </div >
    <!-- <input ref="note1" v-model="note" style="width:50%;background: green;position: fixed;left:0px">
    <input ref="note2" v-model="note" style="width:50%;background: red;position: fixed;left:0px"> -->

<div class="slide">
  <!-- <label>信息总览：</label> -->
	<ul class="slide-auto">
		<li>{{note}}</li>
		<li>{{note}}</li>
		
	</ul>
</div>
  
</div>


</body>
</template>
<script>

import '../../../static/loadsh.js'
import { clearInterval, setInterval } from 'timers';
import {reqMessage} from './api'
import axios from 'axios'

export default {
    name:"Home",
    data() {
    return {
      showInputNumerOfItems:false,
      optionValue:0,
      selectList:[0.5,1,1.5,2,2.5,5],
      speedInput:"1.5",
      // 用来翻动数据
      setInterval1:0,
      // 用来定期发请求
      setInterval2:0,
      setInterval3:0,
      showLoading:true,
      upDataTime:"... ... ...",
      items:[],
      nextNums:[],
      beAbandoned:[],
      note:"",
      tempTime:0,
      itemsInput:"",
      itemsOnWeb:11

    } 

    },
    methods: {
      inputItems:function(){
        this.showInputNumerOfItems=!this.showInputNumerOfItems
        this.itemsOnWeb=this.itemsInput
      },
      clickBody:function(){
        // alert("你点哦我了")
        if(this.setInterval1!=0){
          clearInterval(this.setInterval1)
          this.setInterval1=0

        }else{
          this.setInterval1=setInterval(() => {
            if(this.nextNums.length!=0){
              this.scroll()
            }else{
              window.console.log("数据还没加载出来")
            } 
          }, 1000);

        }


      },
     
      test:function(){
         axios.request({
          method:"get",
          url:'./api'+"/latest_data/"
        }).then((v)=>{
      window.console.log(v.data)
      
      // var lenOfThisW=document.body.scrollWidth
      // this.$refs.note2.style.left=String(lenOfThisW)+"px"
    })
   
     
      
       
    
      },

      sureSpeed:function(){
        console.log("ddd")
     
        window.clearInterval(this.setInterval1)
        // this.setIntervals.splice(0,1)
        // this.speed=parseFloat(this.speedInput)
        // // clearInterval
        this.setInterval1=setInterval(() => {
            this.scroll()
        }, parseFloat(this.selectList[this.optionValue-1] )*1000);
        // this.setIntervals.push(myInterval)

      },


     scroll:function () {
        //  this.remove();
        //  this.add()
        const tempItems=[]
        for (var i=0;i<this.itemsOnWeb;i++){
          tempItems.push(this.nextNums[0])
          this.nextNums.push(this.nextNums[0])
          this.nextNums.splice(0,1)

        }
        this.items=tempItems;
             
     },   
    //  对原数据进行个性化操作
 changDataToNums:function(rowData){
   var tempnextNums=[]
   for (var thisindex=0;thisindex<rowData.length;thisindex+=1){
     var thisD=rowData[thisindex]
     if(thisD.paras==1){
       thisD.paras="连班"
     }else{
       thisD.paras=null
     }
     if(thisD.business==1){
       thisD.business="是"

     }else{
       thisD.business="否"
     }

    if(thisD.IsVip==1){
    thisD.IsVip="是"

     }else{
       thisD.IsVip="否"
     }

    if(thisD.resulttype==1){
    thisD.resulttype="成功"

    }else if(thisD.resulttype==0){
      thisD.resulttype="失败"
    }
    else{
      thisD.resulttype="已分配"
    }

    if(thisD.nation==1){
      thisD.nation="国内"

    }else{
      thisD.nation="国际"

    }
    // thisD.resulttype="jbwy"
    tempnextNums.push(thisD)
   }
   this.nextNums=Array.prototype.slice.call(tempnextNums)
    this.showLoading=false
 }
  },
  mounted:function(){
 
  },
  created:function(){
    
   axios.request({
         headers:{
          'Access-Control-Allow-Origin':"*",
          'Access-Control-Allow-Headers':"X-Requested-With",
          'Access-Control-Allow-Headers': "Content-Type",
          'Access-Control-Allow-Methods':"PUT,POST,GET,DELETE,OPTIONS",
          'X-Powered-By':'3.2.1',
          'Content-Type': "application/json;charset=utf-8"


      },
        
          method:"get",
          // './api' 
        // url:'./api'+"/latest_data/",
           url:"http://47.92.202.234:8008/latest_data/",
      
        }).then((v)=>{
      window.console.log(v.data.data)
      this.changDataToNums(v.data.data)
      
      this.upDataTime=v.data.id
      this.note=v.data.note
      // var lenOfThisW=document.body.scrollWidth
      // this.$refs.note2.style.left=String(lenOfThisW)+"px"
    })
    
    this.setInterval1=setInterval(() => {
      if(this.nextNums.length!=0){
        this.scroll()
      }else{
         window.console.log("数据还没加载出来")
      } 
    }, 1000);
    

  


    this.setInterval2=setInterval(()=>{
      axios.request({
         headers:{
          'Access-Control-Allow-Origin':"*",
          'Access-Control-Allow-Headers':"X-Requested-With",
          'Access-Control-Allow-Headers': "Content-Type",
          'Access-Control-Allow-Methods':"PUT,POST,GET,DELETE,OPTIONS",
          'X-Powered-By':'3.2.1',
          'Content-Type': "application/json;charset=utf-8"


      },
        
          method:"get",
          // './api' 
          // url:'./api'+"/latest_data/",
           url:"http://47.92.202.234:8008/latest_data/",
      
        }).then((v)=>{
     if(v.data.id!=this.upDataTime){

       
      //  this.nextNums=v.data.data
      this.changDataToNums(v.data.data)
    
     }
      // this.upDataTime=v.data.    
    })

    },5000);
  
  // this.setInterval3=setInterval(() => {
  //    if(this.nextNums.length!=0){
  //     //  alert(this.$refs.note2.getBoundingClientRect().x)
  //     this.tempTime+=50
  //     this.$refs.note1.style.left="-"+String(this.tempTime)+"px"
  //     console.log("-"+String(this.tempTime)+"px")
  //     this.$refs.note2.style.left=String( document.body.scrollWidth-this.tempTime)+"px"
  //     console.log(this.$refs.note2.getBoundingClientRect())

  //     if(this.$refs.note2.getBoundingClientRect().x<0){
  //     // // console.log(this.$refs.note2.style.right)
  //     this.$refs.note1.style.left="0px"
  //     this.$refs.note2.style.left=String( document.body.scrollWidth)+"px"
  //     // this.$refs.note2.setBoundingClientRect().x=document.body.scrollWidth


  //     }
      
        
  //     }else{
  //        window.console.log("数据还没加载出来不要动")
  //     } 
    
  // }, 1000);
  },
  // 发送axios请求初始化数据

}
</script>

<style>
/* #menu{
	margin-left: 210px;
	margin-top: 190px;
	font-family:"楷体";
	font-size: 25px;
	color: rgb(61, 236, 76);
} */

.tdstyle1{
  /* color:  */
  background-color: rgb(0, 255, 13);
  font-weight: bolder

}
.tdstyle2{
  /* 失败 */
  color: rgb(17, 16, 16);
  background-color: rgb(247, 131, 179);
  font-weight: bolder

}
.tdstyle3{
  /* 已分配 */
  color:rgb(17, 16, 16);
  background-color: aliceblue;
  font-weight: bolder;
  /* display: inline; */
 
  

}


.list-complete-item {
/* 这里控制数据排放的方向 */
  transition: all 2s;
  display: block;
  margin-right: 30px;

  
}
.list-complete-enter, .list-complete-leave-to
/* .list-complete-leave-active for below version 2.1.8 */ {
  opacity: 0;
  /* 这里控制进入离开的方向 */
  transform: translateX(120px);
}
.list-complete-leave-active {
  position: absolute;
}

.tablediv{

    /* width:590px; */
    /* height: 350px; */
    /* margin-left:5%; 
    margin-right: 5%; */
  /* height: 50%; */
   margin-top: 0px;
   margin-bottom: 0px;
   
   
    

}
.slide {
	position: fixed;
  bottom: 10px;
	margin: auto;
	width: 1500px;
	/* height: 50px; */
	overflow: hidden;
	color: rgb(17, 16, 16);
  font-size: 18px;
	text-align: center;
  font-weight: bolder;
  /* background-color: rgba(250, 10, 42, 0.966) */
  /* margin-top: 20px */
  
}
.slide ul {
	margin: 10px 0;
	padding: 0;
	width: 9999px;
	transition: all 0.5s;
  text-align: center;
  
  
  
}
.slide .slide-auto {
	animation: marginLeft 20s infinite;
}
.slide li {
	float: left;
	width:1500px;
	/* height: 200px; */
	list-style: none;
  color: red
 
	/* line-height: 200px; */
}
.slide li:nth-child(1){
	/* background: #ac24bb; */
}
.slide li:nth-child(2){
	/* background: #be65ae */
}
.slide li:nth-child(3){
	/* background: #cc8674; */
}
@keyframes marginLeft {
	0%, 25%{
		margin-left: 0;
	}
	25%, 50% {
		margin-left: -50%;
	}
	50%, 75% {
		margin-left: -100%;
	}
	75%, 99.9% {
		margin-left: -50%;
	}
	99.9%, 100% {
		margin-left: -100%;
	}
}


/* .flip-list-move {
  transition: transform 1s;
} */

</style>



